/** Font Sizing **/
/** Base Styles **/
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  vertical-align: middle;
}

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

audio:not([controls]) {
  display: none;
}

[hidden] {
  display: none;
}

/** Base Typography **/
body {
  color: #222;
  font-family: "Roboto", helvetica, arial, sans-serif;
  font-size: 15px;
  line-height: 1.55em;
}

a,
a:visited {
  color: #222;
  text-decoration: none;
}

a:hover,
a:visited:hover {
  color: #09f;
}

a,
a:link,
a:hover,
input[type="submit"],
input[type="submit"]:hover,
.transition,
.transition:hover {
  transition: all 0.3 ease;
}

.cf {
  *zoom: 1;
}

.cf:before, .cf:after {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

.no-select {
  user-select: none;
}

.vct, .vctr, .vctd {
  display: table;
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

.vctr {
  display: table-row;
}

.vctd {
  display: table-cell;
}

.row-height {
  display: table;
  table-layout: fixed;
  height: 100%;
  width: 100%;
}

.col-height {
  display: table-cell;
  float: none;
  height: 100%;
}

.col-top {
  vertical-align: top;
}

.col-middle {
  vertical-align: middle;
}

.col-bottom {
  vertical-align: bottom;
}

.eq-height {
  display: flex;
  width: 100%;
}

.eq-height > * {
  flex: 1;
}

.eq-height > * .thing-inside {
  height: 100%;
}

h1, h2, h3, h4 {
  font-family: "Roboto", helvetica, arial, sans-serif;
  font-weight: normal;
  line-height: 1.2em;
}

h1 {
  font-size: 35px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 20px;
}

p {
  font-size: 15px;
  margin-bottom: 2.25em;
}

/** Layouts **/
/* =============================================================================
   Header
   ========================================================================== */
.header {
  position: relative;
  z-index: 100;
}

.brand {
  width: 200px;
  float: left;
}

/* =============================================================================
   Navigation
   ========================================================================== */
.navigation {
  display: inline-block;
  font-size: 0;
  float: right;
}

.navigation li {
  display: inline-block;
}

.navigation a {
  font-size: 16px;
  line-height: 30px;
  display: block;
  padding: 0 10px;
}

.navigation .dropdown {
  position: relative;
}

.navigation .dropdown-wrapper {
  display: none;
  position: absolute;
  top: 30px;
  right: 0;
}

.navigation .dropdown-wrapper li {
  display: block;
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
}

.navigation .dropdown-wrapper li:last-child {
  border-bottom: 0;
}
